/* Mobile Styles */
@media only screen 
and (max-device-width: 736px)  { 
    .form-footer {
        display:-ms-flexbox;
        display:flex;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        padding: 2rem;
        margin: 0 auto;
        width: 80%;
        background-color: hsl(322, 21%, 40%);
        -webkit-box-shadow: 0 8px 8px rgba(0,0,0,0.12), 0 8px 8px rgba(0,0,0,0.24);
                box-shadow: 0 8px 8px rgba(0,0,0,0.12), 0 8px 8px rgba(0,0,0,0.24);
        -webkit-transition: all 0.3s cubic-bezier(.25,.8,.25,1);
        -o-transition: all 0.3s cubic-bezier(.25,.8,.25,1);
        transition: all 0.3s cubic-bezier(.25,.8,.25,1);
      }

    .bye-footer {
        text-align: center;
        width: 80%;
        margin: 0 auto;
        color: #B9CCB8;
        font-size: 2rem;
    }
}

/* Desktop Styles */
@media screen 
and (min-device-width: 737px)  {
    .form-footer {
        display:-ms-flexbox;
        display:flex;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        padding: 2rem;
        margin: 0 auto;
        width: 80%;
        background-color: hsl(322, 21%, 40%);
        -webkit-box-shadow: 0 8px 8px rgba(0,0,0,0.12), 0 8px 8px rgba(0,0,0,0.24);
                box-shadow: 0 8px 8px rgba(0,0,0,0.12), 0 8px 8px rgba(0,0,0,0.24);
        -webkit-transition: all 0.3s cubic-bezier(.25,.8,.25,1);
        -o-transition: all 0.3s cubic-bezier(.25,.8,.25,1);
        transition: all 0.3s cubic-bezier(.25,.8,.25,1);
    }

    .bye-footer {
        text-align: center;
        width: 80%;
        margin: 0 auto;
        color: #B9CCB8;
        font-size: 2rem;
    }
}


