/* Mobile Styles */
@media only screen 
and (max-device-width: 736px)  { 
  #home-page {
      background-image: none;
      background-color: #B9CCB8;
      width: 100%;
      height: 100%;
  }

  #announcement-container {
    background-color: #B9CCB8;
  }
}

/* Desktop Styles */
@media only screen and (min-width: 961px) {
    #home-page {
        width: 100%;
        height: 100%;
        position: absolute;
        background-size: cover;
    }
}


