/* Mobile Styles */
@media only screen 
and (max-device-width: 736px) { 
    #rsvp-page {
        position: absolute;
        width: 100%;
        height: 100%;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        -ms-flex-line-pack: center;
            align-content: center;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        background-color: rgba(125, 81, 109, 1.0); /* Cosmic */;
    }
}


@media screen 
and (min-device-width: 737px)  { 
    #rsvp-page {
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 0;
        -ms-flex-line-pack: center;
            align-content: center;
        -ms-flex-align: center;
            align-items: center;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
    }
}


