.iron-image-container {
  position: relative;

  width: 100%;
  height: 100%;

  overflow: hidden;
}

.iron-image-preload {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  background-size: cover;
}

.iron-image-loaded {
  position: fixed;
  max-width: 100vw;
  margin-top: 3rem;
  height:auto;
  width:auto;
  max-height:100vh;
  z-index: 2;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  background-size: cover;

  opacity: 0;

  -webkit-transition: opacity 1s ease;

  -o-transition: opacity 1s ease;

  transition: opacity 1s ease;
}

.iron-image-fade-in {
  opacity: 1;
}


