/* Mobile Styles */
@media only screen 
and (max-device-width: 736px)  { 
    #attending-footer {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        width: 35rem;
        margin: 0 auto;
    }
}

/* Desktop Styles */
@media screen 
and (min-device-width: 737px)  { 
    #attending-footer {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        width: 30rem;
        margin: 0 auto;
    }
}


